import {
			mapMutations
	} from 'vuex';
	var _this;

	export default {
		data() {
			return {
				phoneData:'', //用户/电话
				passData:'', //密码
				logining: false
			};
		},
		mounted() {
			_this= this;
			this.isLogin();
		},
		methods: {
			...mapMutations(['login','getMsg']),
			isLogin(){
				//判断缓存中是否登陆过，直接登陆
				try {
					const value = uni.getStorageSync('setUserData');
					if (value) {
						//有登陆信息
						console.log("已登录用户：",value);
						_this.$store.dispatch("setUserData",value); //存入状态
						uni.reLaunch({
							url: '../index/index',
						});
					}
				} catch (e) {
					// error
				}
			},
		  async startLogin(){
				//登陆
				if (this.phoneData.length == "") {
					uni.showToast({
						icon: 'none',
						position: 'bottom',
						title: '用户名不能为空'
					});
					return;
				}
				if (this.passData.length < 5) {
					uni.showToast({
							icon: 'none',
							position: 'bottom',
							title: '密码需大于5位数'
					});
					return;
				}
				this.logining = true;

				const login =  await this.$request.post('/auth/login', {
					data: {
						mobile:this.phoneData,
						password: this.passData
					}
				});

				const usermsg = await this.$request.post('/user/user-info',{
					header: {
						'token': login.data.data.token
					},
				})

				if (login.data.code === 0) {
					uni.showToast({
							icon: 'none',
							position: 'bottom',
							title: '登录成功'
					});
					this.login(login.data.data);  //用户信息存入vuex
					this.getMsg(usermsg.data.data);  //用户信息存入vuex
					uni.navigateBack();
				} else{
					uni.showToast({
							icon: 'none',
							position: 'bottom',
							title: login.data.msg
					});
					this.logining = false;
				}

			},
			// 微信登录
			wxLogin(){
				uni.login({
					provider: 'weixin',
					success: function (loginRes) {
						console.log(loginRes.authResult);
					}
				});
			}
		}
	}
